import { ethers, utils } from 'ethers'
import { useState, useEffect, useRef } from 'react';
import { abi, contractAddress } from './Var';

function OnClickConnect(){
    
    function defaults1(){
        let m = Array(14)
        m[0] = 'User Wallet Address'
        m[1] = 'Network Name'
        m[2] = 'Network Chain ID'
        //'Registered'//default is false which means registered
        m[3] = false
        m[4] = 'RegFree'
        m[5] = 'AmountToReg'
        m[6] = 'Current Lottery'
        m[7] = 'Start Lottery'
        m[8] = 'End Lottery'
        m[9] = 'Lottery Balance'
        m[10] = 'Last Lottery'
        m[11] = 'Last Lottery Lucky Number'
        m[12] =  'BusdAmountPerBet'
        m[13] = 'User ID'

        setUserDetails(m)
        //a(m)
        /*
        0:bool: registered false
        1:bool: regFree true
        2:uint256: _amountToReg 1
        3:uint256: currentLottery 8720
        4:uint256: startLotteryTime 0
        5:uint256: endLotteryTime 0
        6:uint256: lotteryBalance 0
        7:uint256: lastLottery 111
        8:string: lastLotteryNumber not yet
        9:uint256: amountPerBet 1
        10:uint256: userID 0
         */
        
        getDefaultOnChain()
    }

    let messageWeb3 = "Use Browser that supports Web3 and Account Connected"

    async function getDefaultOnChain(){
        ab('Getting Data OnChain...')
        try{
            // @ts-ignore
            const { ethereum } = window
            if(ethereum){
                const provider = new ethers.providers.Web3Provider(ethereum)
                const signer = provider.getSigner();
                const contract = new ethers.Contract(contractAddress, abi, signer)

                const datas = await contract.getKeyDatas()
                //await datas.wait()
                //ab(`Transaction successful with hash: ${datas.hash}`)
                ab(`Data are: ${datas}`)

                //let m = [...user]
                //a([...user])

                //m[3] = datas[1]
                let registered = datas[0]
                if(registered) setNotRegistered(false)
                else setNotRegistered(true)
            }else{
                setReportOnce(messageWeb3)
            }
            
            //setNotRegistered(!datas[1])
            //setUserDetails(m)

            //ab(m[3])
            //ab(datas[1])
            //ab()
    
            //const transaction = await contract.approve(spender_, amountToWithdraw)
            
            //await transaction.wait()
            
            //ab(`Transaction successful with hash: ${transaction.hash}`)
        }
        catch(err){
            ab(err)
            setReportOnce(err)
            /*if( err?.error?.reason !== undefined ){
                let error = JSON.stringify(err.error.reason) +'\n'+ JSON.stringify(err.error.code) //JSON.parse(JSON.stringify(err));
                ab(`Error occured: ${error}`)
            }
            else{
                ab(`Error occured: ${JSON.stringify(err.message)}`)
            }*/
            
        }
    }

    function setReportOnce(data){
        setModal(true)
        setReport(data)
    }

    async function loaded(){
        
            // @ts-ignore
        const { ethereum } = window
        if(ethereum){
            try {
                // Will open the MetaMask UI
                // You should disable this button while the request is pending!
                await ethereum.request({ method: 'eth_requestAccounts' });
                const accounts = await ethereum.request({method: 'eth_accounts'});
                const provider = new ethers.providers.Web3Provider(ethereum)
                const network = await provider.getNetwork()
                console.log('Network name: '+network.name+" and chainId: "+network.chainId)
        
                let m = [...user]
        
                //a(m)
        
                if(accounts.length !== 0){
                // @ts-ignore
                const account = accounts[0];
                m[0] = account
                m[1] = network.name
                m[2] = network.chainId.toString()
                
                setUserDetails(m)
        
                }else{
        
                }
        
                } catch (error) {
                    //setAddress(JSON.stringify(error.toString()))
                    let bb = JSON.stringify(error.toString())
                    setReportOnce(bb)
                    //console.log(error);
                }
        }else{
            setReportOnce(messageWeb3)
        }
        
    }


    useEffect(() => {
        defaults1()
        loaded()
        getForRef()

        function handler3(event){
            if(!modalRef.current) return 
            // @ts-ignore
            if(!modalRef.current.contains(event.target)){
                setModal(false)
            }
        }

        function changy(event){
            if(document.visibilityState == "visible"){
                a('tab is active')
                refreshData()
            }
            else
                a('tab is not active')
        }

        document.addEventListener('click', handler3, true)
        //document.addEventListener('visibilitychange', changy, true)

        return () => {
            document.removeEventListener('click', handler3)
            //document.removeEventListener('visibilitychange', changy)
        }
        
      }, [])

      const modalRef = useRef()

      let addressGreet =    "0xa513E6E4b8f2a923D98304ec87F64353C4D5C853"
      let userAddress =     "0xa673E6E4b7f2a923D98304ec87F64353C4D5C853"
      let addressDeployer = "0x67CE342a923D978E808304ec87F64353C4D5C853"

      function getForRef(){
          let currentURL = window.location.href//getCu
        let paramString = currentURL.split('?')[1]
        let queryString = new URLSearchParams(paramString)
        // @ts-ignore
        let size = queryString.size
        if(size != 0){
            for(let pair of queryString.entries()){
                if(pair[0] == "ref"){
                    setRefID(pair[1])
                    setShowRefID(pair[1])
                    return;
                }
    
                /*a(`Key is: ${pair[0]}`)
                a(`Value is: ${pair[1]}`)*/
            }
        }
        
      }

      /**
       * 
       * {"code":-326,"message":"Internal JSON-RPC error.","data":{"code":-32603,"message":"Error: VM Exception while processing transaction: reverted with reason string 'No active Draw yet'"}
       * {"code":4001,"message":"MetaMask Tx Signature: User denied transaction signature.","stack":"{\n \"code\": 4001,\n \"message\": \"MetaMask Tx Signature: User denied transaction signature.\",\n \"stack\": \"Error: MetaMask Tx Signature: User denied transaction signature.
       * {"code":-323,"message":"Internal JSON-RPC error.","data":{"code":-32603,"message":"Error: VM Exception while processing transaction: reverted with reason string 'Ownable: caller is not the Admin'"},"stack":"{\n \"code\": -32603,\n \"message\": \"Internal JSON-RPC error.\",\n \"data\": {\n \"code\": -32603,\n \"message\": \"Error: VM Exception while processing transaction: reverted with reason string 'Ownable: caller is not the Admin'\"\n }
       * {"code":-393,"message":"Internal JSON-RPC error.","data":{"code":-32603,"message":"Error: VM Exception while processing transaction: reverted with reason string 'Cannot start Lottery when a Lottery is still playing'"},"stack":"{\n \"code\": -32603,\n \"message\": \"Internal JSON-RPC error.\",\n \"data\": {\n \"code\": -32603,\n \"message\": \"Error: VM Exception while processing transaction: reverted with reason string 'Cannot start Lottery when a Lottery is still playing'\"\n }
       * {"code":-603,"message":"Internal JSON-RPC error.","data":{"code":-32603,"message":"Error: VM Exception while processing transaction: reverted with reason string 'Allowance must be equal or more than amount'"},"stack":"{\n \"code\": -32603,\n \"message\": \"Internal JSON-RPC error.\",\n \"data\": {\n \"code\": -32603,\n \"message\": \"Error: VM Exception while processing transaction: reverted with reason string 'Allowance must be equal or more than amount'\"\n }
       * {"code":4001,"message":"MetaMask Tx Signature: User denied transaction signature.","stack":"{\n \"code\": 4001,\n \"message\": \"MetaMask Tx Signature: User denied transaction signature.\",\n \"stack\": \"Error: MetaMask Tx Signature: User denied transaction signature
       * 
       * 
       * 
       */

      
        function handleReferee(event){
            //(e) => this.doSearch(e)
            let k = event.target.value
            //setRefID(k)
            //return;
            a(`onChange: ${k}`)
            if(k.trim().length == 0){
                a(1)
                setRefID("0")
                setShowRefID("0")
            } 
            if(k == 0){
                setRefID("0")
                setShowRefID(k)
            }
            //if(k.toString() == ('')) setRefID("0")
            else if(k>0){
                a(2)
                setRefID(k)
                setShowRefID(k)
            }
                
            else{
                a(3)
                setRefID("0")
                setShowRefID("")
            }

        }

        async function connectWallet(){
            // @ts-ignore
            const { ethereum } = window
            if(!ethereum){//check if ethereum object is found, else alert.
                
                console.log(messageWeb3)
                setReportOnce(messageWeb3)
                return;
            }

            let message = ""
        
            const accounts = await ethereum.request({method: 'eth_accounts'});
            const provider = new ethers.providers.Web3Provider(ethereum)
            const network = await provider.getNetwork()
            console.log('Network name: '+network.name+" and chainId: "+network.chainId)
                    //chainId
              //eth mainnet: 1, ropsten: 3; kovan: 42; rinkeby: 4; bsc(main): 56; 
              //goerli: 5; bsc(test): 97; polygon(matic main): 137; localmachin: 1337
            //this.setContractAddress(network.chainId)
        
            if(accounts.length !== 0){
              const account = accounts[0];
              message = `browser supported and authorized account found: ${account}`;
              console.log(message);
        
            }else{
                message = "browser support but No authorized account found. Try again..."
                console.log(message);
            }
        
            return ethereum;
        }

        async function registerAccount(){
            //a('Register account Pressed')
            a(refID)
            setForButton(true)
            setSlider(true)
            //setVisibleBtn1('hidden')
            setTimeout(() => {
                //setForButton(false)
                //setSlider(false)
                //setVisibleBtn1('visible')
            }, 2000)
            //a(getCurrentPageURL())
            ab('registering...')
            connectWallet()
            // @ts-ignore
            const { ethereum } = window
            try {
            const provider = new ethers.providers.Web3Provider(ethereum);
                const signer = provider.getSigner()
                const contract = new ethers.Contract(contractAddress, abi, signer)
                const transaction = await contract.registerPlayer(refID)
                await transaction.wait()
                console.log('transaction hash: '+transaction.hash)
                setForButton(false)
                setSlider(false)
                setReportOnce("Tx Successful")
                
                //show modal here for each tx

    }catch(err){
        //a(err)
        setForButton(false)
        setSlider(false)
        setModal(true)

        if(err?.data?.message != undefined){
            let error = JSON.stringify(err.data.message)
            //let mySubString = error.substring(error.indexOf("'")+1, error.lastIndexOf("'"))
            error = getStringOut(error)
            ab(`Error occured1: ${error}`)
            
            setReportOnce(error)

            return
        }
        if(err?.error?.reason != undefined){
            let error = JSON.stringify(err.error.reason) +'\n'+ JSON.stringify(err.error.code)
            ab(`Error occured2: ${error}`)
            setReportOnce(error)
            return
        }
        if(err?.message != undefined){
            ab(`Error occured4: ${err.message}`)
            setReportOnce(err.message)
            return
        }
    }
        }

        function getStringOut(data){
            return data.substring(data.indexOf("'")+1, data.lastIndexOf("'"))
        }

        const [user, setUserDetails] = useState([])//address, network, chainId,
        const [refID, setRefID] = useState("0")
        const [showRefID, setShowRefID] = useState("")
        const [forButton, setForButton] = useState(false)
        const [slider, setSlider] = useState(false)
        //const [visibleBtn1, setVisibleBtn1] = useState('visible')
        const sliderImg = <img style={{}} className='w3-spin' src='graphics/icons8-loader-30.png' width={25} height={25} />
        const [modal, setModal] = useState(false)
        const [report, setReport] = useState("")
        const [notRegistered, setNotRegistered] = useState(false)

        const web3_unavailable = <a href='https://trustwallet.com' target={'_blank'}><button style={{marginLeft: '0px',}} 
                                                    className='w3-button w3-hover-amber w3-teal '>Browser not Supported</button></a>

        function hideModal(){
            setModal(false)
        }

        function showModal(){
            return (
                <div style={{display: 'block'}} className='modal'>
                <div ref={modalRef} className='modal-content w3-animate-zoom'>
                    <span onClick={hideModal} className='close'>&times;</span>
                    <h5 style={{color: "#000"}}>Report:</h5>
                    <h4 style={{color: "#000", fontWeight: 'bolder'}}>{report}</h4>
                    
                </div>
            </div>
            )
        }

        function refreshData(){
            defaults1()
            loaded()
            //getDefaultOnChain()
        }

      return (
        <>

            {/*<div style={{/*border: '1px solid #fff'/}} className="w3-center w3-center w3-padding-small w3-small w3-round-xlarge">
                <tr>
                    <th>Name</th>
                    <th>Value</th>
                </tr>
                <tr>
                    <td>Network Name</td>
                    <td>{"jskjfd"}</td>
                </tr>
                <tr>
                    <td>Eve</td>
                    <td>Network Name Network NameNetwork Name Network NameNetwork Name Network NameNetwork Name Network Name</td>
                </tr>
                <tr>
                    <td>Adam</td>
                    <td>0xa513E6E4b8f2a923D98304ec87F64353C4D5C853 0xa513E6E4b8f2a923D98304ec87F64353C4D5C853 Network Name Network NameNetwork Name Network Name</td>
                </tr>
            </div> */}
            {modal && showModal()}                      

            {notRegistered && <div className='w3-row w3-center w3-padding-small w3-border w3-small w3-round-xlarge w3-margin'>
                <div className='' style={{fontSize: '13px'}}>Referee(optional)</div>
                <div><input placeholder='0' value={showRefID} onChange={(e) => handleReferee(e)} className="w3-round-xlarge w3-large w3-center" 
                                    style={{width: '25%', fontSize: '10', margin: '2px'}}/></div>

                {/*close && <div className='w3-row w3-center'>
                        <span className='w3-padding-small w3-small w3-red w3-round-xlarge'>{error}</span></div> */}                                

                <div className='w3-center'><button disabled={forButton} style={{marginTop: '7px', fontSize: '13px', /*visibility: visibleBtn1*/}} 
                    className="w3-button w3-round-xlarge w3-padding-small {6785} w3-teal menuHover" onClick={registerAccount}><b>Register</b></button>
                    {slider && sliderImg}
                    </div>

            </div>}

            <div className="w3-row w3-center w3-padding-small w3-border w3-small w3-round-xlarge w3-margin">
          <div>Network Name: <b>{user[1]}</b></div>

          <div className=''>Address: <span className='w3-text-white'>{user[0]}</span></div>
                <div className=''>Network Name: {user[1]}</div>
                <div className=''>ChainID: {user[2]}</div>
          
          <div style={{marginTop: '10px'}}>Contract Address: <b><a target={'_blank'} 
          href={'address/'+addressGreet}><span style={{fontSize: '10px'}} 
          className=''>{addressGreet}</span></a></b></div>
            
          <div style={{marginTop: '10px'}}>Deployed By: <b><a target={'_blank'} 
          href={'address/'+addressDeployer}><span style={{fontSize: '10px'}} 
          className=''>{addressDeployer}</span></a></b></div>

          <div style={{marginTop: '10px'}}>User Address: <b><a target={'_blank'} 
                href={'address/'+userAddress}><span style={{fontSize: '10px'}} 
                className=''>{userAddress}</span></a></b></div>

            
            </div>  
          
            <div className='w3-center'><button style={{margin: '2px', fontSize: '13px'}} 
                    className="w3-button w3-round-xlarge w3-padding-small w3-teal menuHover" onClick={refreshData}><b>Refresh</b></button></div>
        </>
    )

    function a(v){console.log(v)}
    function ab(v){console.log(v)}
}

export {OnClickConnect}